import React from "react";

export const NOOP = () => null;

export const preventDefault = (
  e: React.MouseEvent<HTMLElement, MouseEvent>
) => {
  e.preventDefault();
  e.stopPropagation();
};

/**
 * Returns a valid HTML tag which can be used for building components.
 * @param tagName
 */
export const getValidTagName = (tagName?: keyof HTMLElementTagNameMap) => {
  let Tag = "div";
  if (
    tagName !== undefined &&
    tagName !== "applet" &&
    tagName !== "basefont" &&
    tagName !== "dir" &&
    tagName !== "font" &&
    tagName !== "frame" &&
    tagName !== "frameset" &&
    tagName !== "marquee" &&
    tagName !== "slot"
  ) {
    Tag = tagName;
  }
  return Tag;
};

/**
 * Prepend a character to string till it reaches a specified length
 * @param str isRequired, string
 * @param length optional (2), length of final string
 * @param char optional ('0'), character to be prepended to attain specified length.
 * @returns string
 */
export const prependCharToString = (str: string, length = 2, char = "0") => {
  while (str.length < length) str = char + str;
  return str;
};

/**
 * Append a character to string till it reaches a specified length
 * @param str
 * @param length
 * @param char
 */
export const appendCharToString = (str: string, length = 2, char = "0") => {
  while (str.length < length) str += char;
  return str;
};
