import React from "react";
import { faChevronDown, faChevronRight } from "@faharmony/icons";
import { motion, AnimatePresence } from "framer-motion";
import { Theme } from "@faharmony/theme";
import { useDisableAnimation } from "@faharmony/hooks";
import { Text } from "../Text";
import { Box } from "../Box";
import { Grid } from "../Grid";
import { Icon } from "../Icon";
import {
  IAccordionOptions,
  IAccordionPanelProps,
  IAccordionProps,
} from "./types";

const PanelHeader = (
  props: IAccordionPanelProps &
    IAccordionOptions & {
      isExpanded: boolean;
    }
) => {
  const {
    isExpanded,
    handleExpand,
    handleCollapse,
    heading,
    id,
    action,
    disabled,
  } = props;
  return (
    <header
      style={{
        position: "sticky",
        top: 0,
        zIndex: 1,
        width: "100%",
        minHeight: `calc(3.5 * ${Theme.spacing()})`,
        height: "auto",
        padding: `${Theme.spacing("sm")} ${Theme.spacing()}`,
        borderRadius: Theme.radius("sm"),
        cursor: disabled ? "initial" : "pointer",
        background: Theme.colorBackground("rowAlt"),

        // background: isExpanded
        //   ? Theme.colorBackground("rowAlt")
        //   : "transparent",
      }}
      onClick={
        disabled
          ? undefined
          : () => (isExpanded ? handleCollapse(id) : handleExpand(id))
      }
    >
      <Grid gridTemplate=" auto / auto max-content">
        <Box justifyContent="flex-start">
          <Text
            value={heading}
            variant="strong"
            // color={isExpanded ? "primary" : "secondary"}
          />
        </Box>
        {(action || !disabled) && (
          <Box justifyContent="flex-end" spacing="md">
            {action && <div>{action}</div>}
            {!disabled && (
              <Icon icon={isExpanded ? faChevronDown : faChevronRight} />
            )}
          </Box>
        )}
      </Grid>
    </header>
  );
};

const AnimatedWrapper = ({
  isExpanded,
  fullHeight,
  fullWidth,
  content,
}: IAccordionPanelProps &
  IAccordionOptions & {
    fullHeight?: IAccordionProps["fullHeight"];
    isExpanded: boolean;
  }) => {
  return (
    <AnimatePresence initial={false}>
      {isExpanded && (
        <motion.section
          className="accordion-panel-content"
          key="content"
          style={{ width: "100%" }}
          initial="collapsed"
          animate="expanded"
          exit="collapsed"
          variants={{
            expanded: {
              opacity: 1,
              height: fullHeight ? "100%" : "auto",
            },
            collapsed: {
              opacity: 0,
              height: 0,
            },
          }}
          transition={Theme.transition()}
        >
          <div style={{ padding: fullWidth ? 0 : Theme.spacing() }}>
            {content}
          </div>
        </motion.section>
      )}
    </AnimatePresence>
  );
};

export const Panel = (
  props: IAccordionPanelProps &
    IAccordionOptions & {
      fullHeight?: IAccordionProps["fullHeight"];
    }
) => {
  const { expandedIds, id, content, fullWidth, fullHeight = false } = props;
  const isExpanded = expandedIds.indexOf(id) > -1;
  const disableAnimation = useDisableAnimation();
  return (
    <Box
      id={"accordion-" + id}
      height={isExpanded && fullHeight ? "100%" : "auto"}
      direction="column"
      alignItems="flex-start"
      className="accordion-panel"
      spacing="none"
    >
      <PanelHeader {...props} {...{ isExpanded }} />

      {disableAnimation ? (
        isExpanded ? (
          <div style={{ padding: fullWidth ? 0 : Theme.spacing() }}>
            {content}
          </div>
        ) : null
      ) : (
        <AnimatedWrapper {...props} {...{ isExpanded }} />
      )}
    </Box>
  );
};
