import React from "react";
import { styled } from "@faharmony/theme";
import { Text, ITextProps } from "../Text";

export const Wrapper = styled.div`
  width: 100%;
`;

interface IInfoBoxProps {
  /**
   * The field name
   */
  field: string;
  /**
   * Content to be shown
   */
  value: React.ReactChild;
  /**
   * Color of value text
   */
  color?: ITextProps["color"];
  /**
   * Is horizontal ?
   * @default false
   */
  horizontal?: boolean;
}

/**
 * InfoBox
 * ---
 * Show information in simple manner
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <InfoBox field="Name" value="Siddhant Gupta" />
 */
const InfoBox = ({
  field,
  value,
  color,
  horizontal = false,
}: IInfoBoxProps) => {
  return (
    <Wrapper>
      <Text variant="h6" value={field} color="primary" />
      {horizontal ? ": " : <br />}
      {typeof value === "string" || typeof value === "number" ? (
        <span style={{ wordBreak: "break-all" }}>
          <Text value={value.toString()} color={color} />
        </span>
      ) : (
        value
      )}
    </Wrapper>
  );
};

export { InfoBox, IInfoBoxProps };
